import React, { useState, useEffect, useReducer } from 'react'

import Breadcrumb from '../../components/Breadcrumb'
import Header from '../../components/Header'

import InputData from './InputData'
import BaselineSummary from './BaselineSummary'
import OptimizedSummary from './OptimizedSummary'
import DetailedReport from './DetailedReport'

import { DashboardContainer } from './styled'

const steps = [
  'Input Data',
  'Baseline Summary',
  'Optimized Summary',
  'Optimized Report',
]

export default function Dashboard() {
  const [navAt, setNavAt] = useState(0)
  const [project, setProject] = useState(null)
  const [optimizer, setOptimizer] = useState(null)
  const [isPresetsReset, setIsPresetsReset] = useState(false)
  const [selectedPreset, setSelectedPreset] = useState('Balanced')
  const [selectedResultIndex, setSelectedResultIndex] = useState(-1)
  const [uploadedFile, setUploadedFile] = useState()
  const [fileName, setFileName] = useState('No file chosen')
  const [constraintsFileName, setConstraintsFileName] =
    useState('No file chosen')
  const [_, forceUpdate] = useReducer((x) => x + 1, 0)

  function optimizePresets(project) {
    if (!project) return
  }

  useEffect(() => {
    if (isPresetsReset) {
      optimizePresets(project)
      setNavAt(1)
      setIsPresetsReset(false)
    }
  }, [isPresetsReset])

  let componentToRender = null

  const baseline = {
    tasks: optimizer?.plannedproject?.tasks,
    resources: optimizer && Object.keys(optimizer?.plannedproject?.resources),
    duration: optimizer?.plannedproject?.output?.projectDurationDays.data,
    critical_paths: optimizer && Object.values(optimizer?.plannedproject?.output?.criticalPath)
  }


  if (navAt === 0)
    componentToRender = (
      <InputData
        fileName={fileName}
        setFileName={setFileName}
        uploadedFile={uploadedFile}
        setUploadedFile={setUploadedFile}
        constraintsFileName={constraintsFileName}
        setConstraintsFileName={setConstraintsFileName}
        optimizer={optimizer}
        setOptimizer={setOptimizer}
        updateProject={(p) => {
          setProject(p)
        }}
        goNext={() => {
          setIsPresetsReset(true)
        }}
      />
    )
  else if (navAt === 1)
    componentToRender = (
      <BaselineSummary
        baseline={baseline}
        constraintsFileName={constraintsFileName}
        optimizer={optimizer}
        selectedPreset={selectedPreset}
        setSelectedPreset={setSelectedPreset}
        selectedResultIndex={selectedResultIndex}
        setSelectedResultIndex={setSelectedResultIndex}
        goPrev={() => {
          setNavAt(navAt - 1)
        }}
        goNext={() => {
          setNavAt(navAt + 1)
          if (selectedResultIndex === -1)
            setSelectedResultIndex(
              optimizer?.results[selectedPreset].length - 1
            )
          // project.optimizer.loss_weights = {
          //   ...optimizationPresets[selectedPreset].optimizer.loss_weights,
          // }
          // project.optimizer.results = [
          //   ...optimizationPresets[selectedPreset].optimizer.results,
          // ]
        }}
      />
    )
  else if (navAt === 2)
    componentToRender = (
      <OptimizedSummary
        baseline={baseline}
        optimizer={optimizer}
        selectedPreset={selectedPreset}
        setSelectedPreset={setSelectedPreset}
        selectedResultIndex={selectedResultIndex}
        setSelectedResultIndex={setSelectedResultIndex}
        tasks={project?.tasks}
        results={optimizer?.results}
        goPrev={() => {
          setNavAt(navAt - 1)
        }}
        goNext={() => {
          setNavAt(navAt + 1)
        }}
      />
    )
  else if (navAt === 3)
    componentToRender = (
      <DetailedReport
        optimizer={optimizer}
        selectedPreset={selectedPreset}
        selectedResultIndex={selectedResultIndex}
        handleSelectResultIndex={(index) => {
          setSelectedResultIndex(index)
        }}
        goPrev={() => {
          setNavAt(navAt - 1)
        }}
        goNext={() => {
          setNavAt(navAt + 1)
        }}
      />
    )

  return (
    <>
      <Header />
      <Breadcrumb
        navAt={navAt}
        steps={steps}
        navigate={(step) => {
          // if (navAt === 1) {
          //   if (selectedResultIndex === -1)
          //     setSelectedResultIndex(
          //       optimizationPresets[selectedPreset].optimizer.results.length - 1
          //     )
          //   project.optimizer.loss_weights = {
          //     ...optimizationPresets[selectedPreset].optimizer.loss_weights,
          //   }
          //   project.optimizer.results = [
          //     ...optimizationPresets[selectedPreset].optimizer.results,
          //   ]
          // }
          // if (step === 0) {
          //   setProject(undefined)
          //   setFileName('Wait for the file to be re-uploaded')
          // }
          setNavAt(step)
        }}
        disabled={
          navAt === 0 ||
          project === undefined
        }
      />

      <DashboardContainer>
        <div className="content">{componentToRender}</div>
      </DashboardContainer>
    </>
  )
}
