import React, { useEffect, useRef } from 'react'
import ReactDOM from 'react-dom'

import * as d3 from 'd3'

import { init_d3 } from '../../utils/plot'
import { plot_color_palette } from '../../utils/color'
import { len, down_sample } from '../../mods/a_utils'

import { Alert, AlertTitle } from '@mui/material'

export default function TaskPlot({
  selectedColor,
  tasks,
  baselineTasks,
  width,
  height,
  color,
  keep = 400,
}) {
  const ref = useRef()
  useEffect(() => {
    // if (!(baselineTasks && len(baselineTasks)) || (!(tasks) && len(tasks))) return
    // if (!tasks) return

    let data = Object.values(tasks).sort((a, b) => a.newStart?.data - b.newStart?.data)
    let baselineData = Object.values(baselineTasks).sort((a, b) => a.plannedStart - b.plannedStart)

    if (data.length > keep) {
      data = data.filter((d, i) => i < 150)
      baselineData = baselineData.filter((bd, i) => i < 150)
    }

    const x_optimized_domain = [
      Math.min(...Object.values(data).map((i) => i.newStart?.data)),
      Math.max(...Object.values(data).map((i) => i.newFinish?.data)),
    ]

    const x_baseline_domain = [
      Math.min(...Object.values(data).map((i) => i.plannedStart)),
      Math.max(...Object.values(data).map((i) => i.plannedFinish)),
    ]

    // const x_domain = x_baseline_domain
    const x_domain = [Math.min(x_optimized_domain[0], x_baseline_domain[0]), Math.max(x_baseline_domain[1], x_optimized_domain[1],),]

    data = down_sample(data, keep)
    baselineData = down_sample(baselineData, keep)

    const y_optimized_domain = [data.length, 0]
    const y_baseline_domain = [baselineData.length, 0]
    const y_domain = [
      Math.max(y_optimized_domain[0], y_baseline_domain[0]),
      Math.min(y_optimized_domain[1], y_baseline_domain[1]),
    ]

    const { svg, x_scale, y_scale } = init_d3({
      margin: 40,
      x_domain,
      y_domain,
      width: width ? width : 560,
      height: height ? height : 500,
      ref,
      // x_label: 'time',
      // y_label: 'task',
      x_type: d3.scaleUtc,
      y_type: d3.scaleLinear,
    })

    const rect_base = svg.append('g')

    rect_base
      .selectAll('rect')
      .data(data)
      .join('rect')
      .attr('x', (d) => x_scale(d.plannedStart))
      .attr('y', (d, i) => y_scale(i))
      .attr('rx', 8)
      .attr('ry', 8)
      .attr('width', (d) => Math.max(x_scale(d.plannedFinish) - x_scale(d.plannedStart), 0))
      .attr('height', y_scale(1) - y_scale(0) - 0.5 - 0.5)
      .attr('fill', (d) => {
        return d.critical ? plot_color_palette.TaskPlot.baselineCriticalTask : plot_color_palette.TaskPlot.baselineTask
      })

    const rect = svg.append('g')

    rect
      .selectAll('rect')
      .data(data)
      .join('rect')
      .attr('x', (d) => x_scale(d.newStart?.data))
      .attr('y', (d, i) => y_scale(i))
      .attr('width', (d) =>
        Math.max(x_scale(d.newFinish?.data) - x_scale(d.newStart?.data), 0)
      )
      .attr('height', y_scale(1) - y_scale(0) - 0.5 - 0.5)
      .attr('rx', 8)
      .attr('ry', 8)
      .attr('fill', (d) => (d.critical ?
        plot_color_palette.TaskPlot.optimizedCriticalTask
        : color ? color : selectedColor))
      .on('mouseover', (event, d) => {
        const { name, newDuration } = d
        const tooltip = (
          <div title={name} style={{
            background: '#eeeeee',
            position: 'absolute',
            top: event.pageY,
            left: event.pageX + 10,
            padding: '6px 8px',
            pointerEvents: 'none',
            borderRadius: '8px'
          }}>
            {name}<br />
            <small>{newDuration.data} hours</small>
          </div>
        )
        ReactDOM.render(tooltip, document.getElementById('tooltip'))
      })
      .on('mouseout', () => {
        ReactDOM.unmountComponentAtNode(document.getElementById('tooltip'))
      })
  }, [tasks, baselineTasks])

  if (!(baselineTasks && len(baselineTasks)) || (!(tasks) && len(tasks))) return null

  const msg =
    len(tasks) > keep
      ? `Chart is only showing up to 150 critical tasks as the project has ${Object.values(tasks).sort((a, b) => a.newStart?.data - b.newStart?.data).length} tasks`
      : ''
  return (
    <>
      {Object.values(tasks).sort((a, b) => a.start_2 - b.start_2).length > 400 ?
        <Alert sx={{ margin: '20px 20px -20px' }} severity="info">
          <AlertTitle>{msg}</AlertTitle>
        </Alert> : ''}
      <div id="tooltip" />
      <svg ref={ref}></svg>
    </>
  )
}