import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3, domains_with_margin } from '../../utils/plot'
import { len } from '../../mods/a_utils'
import { DataGrid } from '@mui/x-data-grid'

export default function StepsPlot({
  results,
  name, x_axis,
  x_label, y_label,
  width = 550,
  height = 400,
  keep = 1000,
  onClick,
  selectedResultIndex
}) {
  const ref = useRef()

  useEffect(() => {
    if (!results?.length) return null

    const data = results
    data.forEach((d, i) => d.idx = i)

    const laborMax = Object.values(results).flat().map(({ output }) => {
      return output.laborDistribution.max
    })

    const domains = domains_with_margin(d3.extent([1, data.length]), d3.extent(name === 'projectDurationDays' ? (data.map((d) =>
      d.output[name])) : name === 'loss' ? data.map(d => d.loss) : name === 'criticalPath' ? data.map(d => d.output.criticalPath.length) : laborMax), 0.1, 0.05)

    const { svg, x_scale, y_scale } = init_d3({
      margin: 40,
      x_domain: domains.x_domain,
      y_domain: domains.y_domain,
      width, height, ref,
      x_label: x_label ? x_label : 'Optimization steps',
      y_label,
      x_type: d3.scaleLinear,
      y_type: d3.scaleLinear,
    })

    const circle = svg.append('g')
    circle.selectAll('circle').data(data).join('circle')
      .attr('cx', (d) => {
        return x_scale(d.idx)
      })
      .attr('cy', (d) => {
        if (name === 'laborDistribution') {
          return y_scale(d.output[name].max)
        } if (name === 'loss') {
          return y_scale(d.loss)
        } if (name === 'criticalPath') {
          return y_scale(d.output.criticalPath.length)
        }
        return y_scale(d.output[name])
      })
      .attr('r', (d, i) => i === selectedResultIndex ? 6 : 3)
      .style('cursor', d => d.option === 'baseline' ? 'auto' : 'pointer')
      .attr('fill', (d, i) => {
        return d.output.constraintLoss > 0
          ? i === selectedResultIndex ? '#db4444' : '#cccccc'
          : i === selectedResultIndex ? '#db4444' : '#00beae'
      }
      )
      .on('click', (e, d) => { onClick(d.idx) })
  }, [results, selectedResultIndex])

  return (
    <>
      <div className='legend'>
        <div className='item'>
          <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
          <div>Step Selected</div>
        </div>
        <div className='item'>
          <div style={{ width: '14px', height: '14px', background: '#00beae', marginRight: '5px', borderRadius: '50%' }}></div>
          <div>Constraints Satisfied</div>
        </div>
        <div className='item'>
          <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
          <div>Constraints Violated</div>
        </div>
      </div>

      <svg ref={ref}></svg>
    </>
  )
}