import React, { useState, useEffect } from 'react'

import { plot_color_palette } from '../../../utils/color'

import TaskPlot from '../../../components/Plot/TaskPlot'
import CombinedPlot from '../../../components/Plot/CombinedPlot'

import { PrimaryButton, SecondaryButton } from 'src/styled'
import { OptimizationContainer, OptimizationCharts } from './styled'

const calculatePercentage = (optimized, baseline) => {
  const diff = (optimized - baseline)
  const percentage = ((diff / baseline) * 100)
  const checkPositive = Math.sign(percentage)
  if (checkPositive === 1) {
    return <span className='badge minus'>{(Math.round(percentage))}%</span>
  }
  else if (checkPositive === -1) {
    return <span className='badge plus'>{(Math.abs(Math.round(percentage)))}%</span>
  }
  else { return <span className='badge'>{(Math.round(percentage))}%</span> }
}

export default function OptimizedSummary({ baseline, optimizer, results, selectedPreset, selectedResultIndex, goPrev, goNext }) {

  const [currentTasks, setCurrentTasks] = useState(Object.values(optimizer.project.tasks).filter((i) => i.newFinish))
  const baselineTasks = Object.values(optimizer.plannedproject.tasks).filter((i) => i.newFinish)

  const selectedPresetResult = results[selectedPreset]
  const selectedPresetResultOutput = results[selectedPreset]?.at(selectedResultIndex)?.output

  const baselineProjectDuration = baseline?.duration

  const newProjectDuration = selectedPresetResultOutput?.projectDurationDays

  const newResources = Object.values(optimizer?.project?.resources)
  const baselineResources = Object.values(optimizer?.plannedproject?.resources)

  const newTasks = Object.values(optimizer?.project?.tasks)

  const baselineCriticalTasks = optimizer?.planned?.criticalPath
  const newCriticalTasks = newTasks.filter((item) => item.critical !== undefined && item.critical).map((d) => d)

  console.log(selectedResultIndex)

  const resourcesData =
  {
    labor: {
      base: [],
      new: []
    },
    material: {
      base: [],
      new: []
    },
    labor_resource_distribution: {
      base: [],
      new: []
    },
    material_resource_distribution: {
      base: [],
      new: []
    }
  }

  if (newResources) {
    newResources.forEach((l) => {
      if (l.type.toLowerCase() === 'labor') {
        resourcesData.labor.new.push({
          name: l.name,
          distribution: l.distribution,
          id: l.id,
          type: l.type,
          show_in_plot: true
        })
        if (l.distribution) {
          Object.entries(l.distribution.xy).map(([key, value]) =>
            resourcesData.labor_resource_distribution.new.push(
              {
                x: key,
                y: value.data
              }
            ))
        }
      }
      if (l.type.toLowerCase() === 'material') {
        resourcesData.material.new.push({
          name: l.name,
          distribution: l.distribution,
          id: l.id,
          type: l.type,
          show_in_plot: true
        })
        if (l.distribution) {
          Object.entries(l.distribution.xy).map(([key, value]) =>
            resourcesData.material_resource_distribution.new.push(
              {
                x: key,
                y: value.data
              }
            ))
        }
      }
    })
  }

  if (baselineResources) {
    baselineResources.forEach((l) => {
      if (l.type.toLowerCase() === 'labor') {
        resourcesData.labor.base.push({
          name: l.name,
          distribution: l.distribution,
          id: l.id,
          type: l.type,
          show_in_plot: true
        })
        if (l.distribution) {
          Object.entries(l.distribution.xy).map(([key, value]) =>
            resourcesData.labor_resource_distribution.base.push(
              {
                x: key,
                y: value.data
              }
            ))
        }
      }
      if (l.type.toLowerCase() === 'material') {
        resourcesData.material.base.push({
          name: l.name,
          distribution: l.distribution,
          id: l.id,
          type: l.type,
          show_in_plot: true
        })
        if (l.distribution) {
          Object.entries(l.distribution.xy).map(([key, value]) =>
            resourcesData.material_resource_distribution.base.push(
              {
                x: key,
                y: value.data
              }
            ))
        }
      }
    })
  }

  useEffect(() => {
    setCurrentTasks(Object.values(optimizer.project.tasks).filter((i) => i.newFinish))
    optimizer.simulate(selectedPresetResult.at(selectedResultIndex).durationRatios)
  }, [selectedPresetResult]) // preset changes here 

  return <OptimizationContainer>
    <h2><span style={{ textTransform: 'capitalize' }}>{selectedPreset}</span> Optimized Summary </h2>
    <div className='summary'>
      <div className='stats'>
        <div className='title'>Project Duration (days)
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(newProjectDuration)} </div>
          <div className='baseline-stats'>{Math.round(baselineProjectDuration)}</div>
        </div>
        {calculatePercentage(newProjectDuration, baselineProjectDuration)}
      </div>
      <div className='stats'>
        <div className='title'>Resources
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(newResources?.length)} </div>
          <div className='baseline-stats'>{Math.round(baseline?.resources?.length)} </div>
        </div>
        {calculatePercentage(newResources?.length, baseline?.resources?.length)}
      </div>
      <div className='stats'>
        <div className='title'>Tasks
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(newTasks?.length)} </div>
          <div className='baseline-stats'>{Math.round(Object.values(baseline?.tasks).length)} </div>
        </div>
        {calculatePercentage(newTasks?.length, Object.values(baseline?.tasks).length)}
      </div>
      <div className='stats'>
        <div className='title'>Tasks on Critical Path
        </div>
        <div className='figure'>
          <div className='numbers'>{Math.round(newCriticalTasks.length)} </div>
          <div className='baseline-stats'>{Math.round(baselineCriticalTasks.length)}  </div>
        </div>
        {calculatePercentage(baselineCriticalTasks.length, newCriticalTasks.length)}
      </div>
    </div>

    {selectedPresetResult ?
      <OptimizationCharts className="multi-charts">
        <div className='full-chart'>
          <h3>Tasks (Overview)</h3>
          <span className="subtitle">Overview of all tasks over time</span>
          <div className='legend'>
            <div className="item">
              <div style={{ width: '14px', height: '14px', background: plot_color_palette.ComparisonPlot[selectedPreset], marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Optimized</div>
            </div>
            <div className="item">
              <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
              <div>Baseline</div>
            </div>
            {/* {len(optimizer.results.at(-1)?.inp?.tasks || {}) <= 400 ?
              <div className="item">
                <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                <div>Critical Path</div>
              </div>
              : <></>
            } */}
          </div>
          {currentTasks ?
            <TaskPlot
              tasks={currentTasks || []}
              width='1150'
              height='600'
              color={plot_color_palette.ComparisonPlot[selectedPreset]}
              baselineTasks={baselineTasks || []}
              title='Tasks (Overview)' />
            : 'No data'
          }
        </div>
        <div className='full-chart'>
          <div className='row'>
            <div>
              <h3>Labor Resources</h3>
              <span className="subtitle">Total utilization of multiple resources over time</span>
            </div>
          </div>
          {resourcesData.labor_resource_distribution ?
            <CombinedPlot
              width="1150"
              selectedResultIndex={selectedResultIndex}
              base_results={resourcesData.labor.base}
              new_results={resourcesData.labor.new}
              base_resource_distribution={resourcesData.labor_resource_distribution.base}
              new_resource_distribution={resourcesData.labor_resource_distribution.new}
              filter='Labor'
              title='Labor Resources' />
            : 'No data'
          }
        </div>

        {resourcesData?.material?.base?.length === 0 ? '' : <>
          <div className='full-chart'>
            <div className='row'>
              <div>
                <h3>Material Resources</h3>
                <span className="subtitle">Total utilization of multiple resources over time</span>
              </div>
            </div>

            {resourcesData.material_resource_distribution ?
              <CombinedPlot
                width="1150"
                selectedResultIndex={selectedResultIndex}
                base_results={resourcesData.material.base}
                new_results={resourcesData.material.new}
                base_resource_distribution={resourcesData.material_resource_distribution.base}
                new_resource_distribution={resourcesData.material_resource_distribution.new}
                filter='Material'
                title='Material Resources' />
              : 'No data'
            }
          </div></>}

      </OptimizationCharts> : ''}
    <div className='row'>
      <SecondaryButton variant='contained' onClick={goPrev}>
        Back
      </SecondaryButton>
      <PrimaryButton variant='contained' onClick={goNext}>
        View Detailed Report
      </PrimaryButton>
    </div>
  </OptimizationContainer>
}