import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import { init_d3, domains_with_margin } from '../../utils/plot'
import { plot_color_palette } from '../../utils/color'

export default function DistibutionPlot({
  results, constraintsFileName,
  baselineResults,
  steps,
  selectedPreset,
  selectedResultIndex = -1,
  width = 550, height = 500,
  k, x_label, y_label, onClick
}) {
  const ref = useRef()

  useEffect(() => {
    if (!Object.values(results).length) return null

    let newData = []
    Object.entries(results).map(([key, value]) => {
      value.map((i, idx) => {
        newData.push({
          idx: idx,
          name: key,
          drtn_day: i.output.projectDurationDays,
          rsrc_max: i.output.laborDistribution.max
        })
      })
    }).flat()

    if (constraintsFileName === 'No file chosen') {
      newData = newData.filter((i) => i.name !== 'Constraint Compliance')
    }

    const projectDays = Object.values(newData).map((x) => {
      return x.drtn_day
    })

    const laborMax = Object.values(newData).map((x) => {
      return x.rsrc_max
    })

    const x_domain = d3.extent(projectDays)
    const y_domain = d3.extent(laborMax)
    const domains = domains_with_margin(x_domain, y_domain, 0.2, 0.2)

    const { svg, x_scale, y_scale } = init_d3({
      x_domain: domains.x_domain,
      y_domain: domains.y_domain,
      margin: 50,
      width, height, ref,
      x_label,
      y_label,
      x_type: d3.scaleLinear,
      y_type: d3.scaleLinear,
    })

    const circleBaseline = svg.append('g')
    circleBaseline.selectAll('circle')
      .data([baselineResults])
      .join('circle')
      .attr('cx', d => x_scale(d.output.projectDurationDays.data))
      .attr('cy', d => y_scale(d.output.laborDistribution.max.data))
      .attr('r', d => 6)
      .attr('z-index', '-1')
      .attr('fill', '#CCCCCC')

    const circle = svg.append('g')
    circle.selectAll('circle')
      .data(newData)
      .join('circle')
      .attr('cx', d =>
        x_scale(d.drtn_day)
      )
      .attr('cy', d => y_scale(d.rsrc_max))
      .attr('r', (d, i) => {
        return (d.idx === steps) - 1 ? 6 : 3
      })
      .style('stroke', (d, i) => {
        return selectedResultIndex === d.idx && selectedPreset === d.name ? '#e61717' : ''
      })
      .attr('fill-opacity', (d, i) => {
        return d.idx / (newData.length <= 40 ? newData.length / 8 : newData.length / 4)
      })
      .attr('fill', (d, i) => {
        const newName = d.name
        return plot_color_palette.ComparisonPlot[newName.toString()]
      })
      .on('click', (e, d) => {
        onClick(d.name, d.idx)
      })
      .style('cursor', 'pointer')
  }, [selectedResultIndex, selectedPreset, results])

  return (
    <svg ref={ref}>
    </svg>
  )
}
