import React, { useState, useEffect, useRef, useReducer } from 'react'
import ReactDOM from 'react-dom'

import * as d3 from 'd3'
import { ToggleButton } from '@mui/material'

import LazyLoad from '../../feat/LazyLoad'

import { ToggleGroup } from '../../pages/Dashboard/OptimizedSummary/styled'
import { limitString } from '../../utils/format'

import { init_d3 } from '../../utils/plot'
import { plot_color_palette } from '../../utils/color'
import { ms_per_day } from '../../mods/a_utils'

export default function CombinedPlot({
  base_results,
  new_results,
  base_resource_distribution,
  new_resource_distribution,
  filter,
  width = 560,
  height = 600,
  // keep = 1000,
}) {
  const ref = useRef()
  let resources = new_results

  const [showResourcePlot, setShowResourcePlot] = useState('optimized')
  const [_, forceUpdate] = useReducer((x) => x + 1, 0)

  useEffect(() => {
    if (!new_resource_distribution) return

    if (showResourcePlot === 'optimized') {
      resources = new_results
    }

    else if (showResourcePlot === 'baseline') {
      resources = base_results
    }

    // base_results.filter((i) => i.name === 'Bulk Excavation and Placement').map((k) => Object.keys(k.distribution.xy).map((x) => console.log(new Date(parseInt(x)))))

    const x_optimized_domain = d3.extent(new_resource_distribution.map((r) => r.x))
    const x_baseline_domain = d3.extent(base_resource_distribution.map((r) => r.x))


    const x_domain = showResourcePlot === 'optimized' ? x_optimized_domain : x_baseline_domain
    const y_optimized_values = (new_resource_distribution).map((r) => {
      return r.y
    })

    const y_optimized_domain = d3.extent(y_optimized_values.flat())
    const y_baseline_values = (base_resource_distribution).map((r) => {
      return r.y
    })

    const y_baseline_domain = d3.extent(y_baseline_values.flat())
    const y_domain = showResourcePlot === 'optimized' ? y_optimized_domain : y_baseline_domain

    const { svg, x_scale, y_scale } = init_d3({
      x_domain,
      y_domain,
      width,
      height,
      ref,
      // x_label: 'time',
      // y_label: 'units',
      x_type: d3.scaleUtc,
      y_type: d3.scaleLinear,
    })

    let x = 0
    for (const r in resources) {
      const rect = svg.append('g')
      rect
        .selectAll('rect')
        .data(Object.entries(resources[r].distribution.xy))
        .join('rect')
        .attr('x', (d) => {
          return x_scale(d[0])
        })
        .attr('width', (d) => {
          return x_scale(ms_per_day) - x_scale(0)
        })
        .attr('y', (d) => y_scale(d[1].data))
        .attr('height', (d) => {
          return y_scale(y_domain[0]) - y_scale(d[1].data)
        })
        .attr('fill', (d, i) => {
          if (resources[r].show_in_plot && resources[r].type === filter) {
            return plot_color_palette.ResourcePlot.allResources[x % plot_color_palette.ResourcePlot.allResources.length]
          }
          else
            return 'none'
        })
        .on('mouseover', (event, d) => {
          const tooltip = (
            <div title={resources[r].name} style={{
              background: '#eeeeee',
              position: 'absolute',
              top: event.pageY,
              left: event.pageX + 10,
              padding: '6px 8px',
              pointerEvents: 'none',
              borderRadius: '8px'
            }}>
              {resources[r].name}
            </div>
          )
          ReactDOM.render(tooltip, document.getElementById('tooltip'))
        })
        .on('mouseout', () => {
          ReactDOM.unmountComponentAtNode(document.getElementById('tooltip'))
        })
      x++
    }

  }, [new_resource_distribution, showResourcePlot, _])

  function toggleSelectResource(r) {
    base_results.filter((item) => item.id === r).map((newItem) => {
      newItem.show_in_plot = !newItem.show_in_plot
    }
    )
    new_results.filter((item) => item.id === r).map((newItem) =>
      newItem.show_in_plot = !newItem.show_in_plot
    )
    forceUpdate()
  }

  if (!new_resource_distribution) return null

  return (
    <>
      <ToggleGroup
        className='row'
        value={showResourcePlot}
        exclusive
      >
        <ToggleButton value='optimized' onClick={() => setShowResourcePlot('optimized')}>
          Optimized
        </ToggleButton>
        <ToggleButton value='baseline' onClick={() => setShowResourcePlot('baseline')}>
          Baseline
        </ToggleButton>
      </ToggleGroup>
      <svg ref={ref}></svg>
      <div id="tooltip" />

      {resources ?
        <ResourceList resources={resources} filter={filter} toggleSelectResource={toggleSelectResource} />
        : ''}
    </>
  )
}

const ResourceList = ({ resources, filter, toggleSelectResource }) => {
  const [visibleCount, setVisibleCount] = useState(Object.values(resources).length > 50 ? 56 : 35)

  const remainingLength = Object.values(resources).length - visibleCount

  const handleLoadMore = () => {
    setVisibleCount(prevCount => prevCount + remainingLength)
  }

  return (
    <>
      <div className="row"><h4>Legend</h4></div>
      <div className="legend">
        {Object.values(resources)
          .filter(r => r.type === filter)
          .slice(0, visibleCount)
          .map((r, i) => (
            <div
              key={r + ' ' + i}
              className="item resources"
              style={{
                opacity: r?.show_in_plot ? '100%' : '50%',
                cursor: 'pointer',
              }}
              onClick={(e, i) => {
                toggleSelectResource(r.id)
              }}
            >
              <div
                style={{
                  width: '14px',
                  height: '14px',
                  background:
                    plot_color_palette.ResourcePlot.allResources[i % plot_color_palette.ResourcePlot.allResources.length],
                  marginRight: '5px',
                  borderRadius: '50%',
                }}
              ></div>
              <div>{limitString(r.name, 20)}</div>
            </div>
          ))}
        {Object.values(resources).filter(r => r.type === filter).length > visibleCount && (
          <LazyLoad onLoadMore={handleLoadMore}>
            <button>Load More</button>
          </LazyLoad>
        )}
      </div>
    </>
  )
}