import React, { useEffect, useState } from 'react'
// import FileDownloadIcon from '@mui/icons-material/FileDownload'

import Modal from 'src/feat/Modal'

import Table from '../../../components/Table'
import ResourcePlot from '../../../components/Plot/ResourcePlot'
import LossPlot from '../../../components/Plot/Loss'
import StepsPlot from '../../../components/Plot/Step'

import { plot_color_palette } from '../../../utils/color'

import { TableContent } from './styled'
import { OptimizationContainer } from '../InputData/styled'
import { OptimizationCharts } from '../OptimizedSummary/styled'
import { PrimaryButton, SecondaryButton } from 'src/styled'

export default function DetailedReport({ optimizer, selectedPreset, selectedResultIndex, handleSelectResultIndex, goPrev }) {
  const [openModal, setOpenModal] = useState(false)
  const data = optimizer?.results[selectedPreset]

  const [currentTasks, setCurrentTasks] = useState(Object.values(optimizer?.project?.tasks))
  const [currentResources, setCurrentResources] = useState(Object.values(optimizer?.project?.resources))

  const selectedPresetResult = optimizer?.results[selectedPreset]

  useEffect(() => {
    setCurrentTasks(Object.values(optimizer?.project?.tasks))
    setCurrentResources(Object.values(optimizer?.project?.resources))
    optimizer.simulate(selectedPresetResult.at(selectedResultIndex).durationRatios)
  }, [selectedResultIndex])

  const changedTasks = currentTasks.filter((item) => {
    return item.newDuration !== undefined && item.newDuration.data !== item.plannedDuration
  })

  const tasks_rows =
    changedTasks && changedTasks.map((task) => {
      const { critical, id, code, name, plannedDuration, newDuration, resources } = task
      const calcRatio = (newDuration?.data / plannedDuration).toFixed(2)
      return ({
        id: id,
        critical: critical,
        name: name,
        code: code,
        old_duration: `${plannedDuration} hours`,
        new_duration: `${newDuration?.data} hours`,
        ratio: calcRatio
      })
    })

  const tasks_columns = [
    {
      field: 'critical', headerName: 'Critical', width: 80, renderCell: (params) => {
        return params.value ? (
          <div className='circle critical' />
        ) : (
          <div className='circle' />
        )
      },
    },
    {
      field: 'code', headerName: 'Code', width: 180, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'id', headerName: 'ID', hide: true },
    {
      field: 'name', headerName: 'Name', width: 500, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial', wordWrap: 'break-word'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'old_duration', headerName: 'Duration (Baseline)', width: 150 },
    { field: 'new_duration', headerName: 'Duration (New)', width: 150 },
    { field: 'ratio', headerName: 'Ratio', width: 50 },
  ]

  currentResources.map((item) => {
    return item.newDuration !== undefined && item.plannedDuration !== item.newDuration?.data
  })

  // filtered changed resources
  const resources_rows =
    currentResources && currentResources.map((resources) => {
      const { name, id, type, code, distribution } = resources
      return ({
        name: name,
        id: id,
        type: type,
        code: code,
        dist: Math.round(Object.values(distribution.xy).map((i) => i.data).reduce((total, current) => total + current, 0)),
        max: Math.round(distribution.max?.data),
        std: Math.round(distribution.span?.data)
      })
    })
  const resources_columns = [
    { field: 'id', headerName: 'ID', width: 80, },
    { field: 'code', headerName: 'code', width: 160, },
    { field: 'type', headerName: 'type', width: 100, },
    {
      field: 'name', headerName: 'Name', width: 400, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'dist', headerName: 'Distribution', width: 130 },
    { field: 'max', headerName: 'Max ', width: 130 },
    { field: 'std', headerName: 'Span', width: 130 },
  ]

  // filtered task-resources relationships
  const filteredTasksResourcesResults = []

  changedTasks && changedTasks.map((task) => {
    Object.values(task.resources).map((resource) => {
      // optimizer?.project?.resources[resource.resourceId].name
      const resourceName = Object.values(currentResources).filter((item) => item.id === resource.resourceId).map((i) => i.name)
      filteredTasksResourcesResults.push({
        critical: task.critical,
        task_name: task.name,
        task_code: task.code,
        task_id: task.id,
        resource_id: resource.resourceId,
        resource_name: resourceName.toString(),
        planned_duration: task.plannedDuration,
        new_duration: task.newDuration?.data,
        units_new_per_hour: resource.newUnitsPerHour?.data,
        planned_units: resource.plannedUnits,
        planned_units_per_hour: resource.plannedUnitsPerHour,
      })
    })
  })

  // plannedUnits: 384 basically means for this entire task what's the total units needed to be done
  // duration: 96 hours
  // then divided = 4

  const tasks_resources_rows = filteredTasksResourcesResults &&
    filteredTasksResourcesResults.map((resources, idx) => {
      const { critical, task_code, task_id, task_name, resource_name, resource_id, planned_duration, new_duration, units_new_per_hour, planned_units, planned_units_per_hour } = resources
      return ({
        critical: critical,
        t_id: task_code,
        id: idx,
        resource_name: resource_name,
        t_name: task_name,
        new_units_per_hour: units_new_per_hour?.toFixed(2),
        old_duration: `${planned_duration} hours`,
        new_duration: `${new_duration} hours`,
        planned_units: `${planned_units}`,
        planned_units_per_hour: planned_units_per_hour?.toFixed(2),
      })
    })
  const tasks_resources_columns = [
    {
      field: 'critical', headerName: 'Critical', width: 80, renderCell: (params) => {
        return (params.value ? (
          <div className='circle critical' />
        ) : (
          <div className='circle' />)
        )
      },
    },
    {
      field: 'id', headerName: 'ID', width: 0, hide: true
    },
    {
      field: 't_id', headerName: 'Task Code', width: 180, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>)
      },
    },
    {
      field: 'resource_name', headerName: 'Resource Name', width: 200, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    {
      field: 't_name', headerName: 'Task Name', width: 350, renderCell: (params) => {
        return (
          <div style={{
            maxHeight: 'inherit', width: '100%', whiteSpace: 'initial'
          }}>{params.value}</div>
        )
      }
    },
    { field: 'old_duration', headerName: 'Duration (Old)', width: 130 },
    { field: 'new_duration', headerName: 'Duration (New)', width: 130 },
    { field: 'planned_units_per_hour', headerName: 'Units (Old)', width: 150 },
    { field: 'new_units_per_hour', headerName: 'Units (New)', width: 150 },
    { field: 'planned_units', headerName: 'Total Planned Units', width: 130 },
  ]

  return (<>
    <div className="row">
      <h2><span style={{ textTransform: 'capitalize' }}>{selectedPreset}</span> Detailed Report</h2>
      <PrimaryButton onClick={() => setOpenModal(true)} variant='contained'>
        Export Report
      </PrimaryButton>
    </div>
    <OptimizationContainer>
      <TableContent>
        <div className='row'>
          <h3>Changed Tasks (Overview)</h3>
          {/* <div><FileDownloadIcon /> Export</div> */}
        </div>
        <div className='legend' style={{ width: '400px', justifyContent: 'flex-start', marginBottom: '20px' }}>
          <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', marginRight: '10px' }}>
            <div className='circle critical' /> Critical
          </div>
          <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
            <div className='circle' /> Non-Critical
          </div>
        </div>
        {currentTasks ?
          <Table rows={tasks_rows} columns={tasks_columns} /> : 'Please select a Optimization Preset before generating your report'}
      </TableContent>

      <TableContent>
        <h3>All Resources</h3>
        <br />
        {currentResources ?
          <Table rows={resources_rows} columns={resources_columns} />
          : 'Please select a Optimization Preset before generating your report'}
      </TableContent>

      <TableContent>
        <h3>Task-Resources</h3>
        <br />
        {filteredTasksResourcesResults ?
          <Table rows={tasks_resources_rows} columns={tasks_resources_columns} /> : 'Please select a Optimization Preset before generating your report'}
      </TableContent>

      <>
        <OptimizationCharts className='multi-charts'>
          <div className='row'><h2>Optimization Iteration Charts</h2></div>
          <div className='chart'>
            <h4>Project Duration</h4>
            <StepsPlot results={data} name='projectDurationDays' x_axis x_label='Optimization steps' y_label='Duration (days)' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          <div className='chart'>
            <h4>Maximum Labour Resources</h4>
            <StepsPlot results={data} name='laborDistribution' x_axis x_label='Optimization steps' y_label='Maximum Resources' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>


          <div className='chart'>
            <h4>Critical Path Tasks</h4>
            <StepsPlot results={data} name='criticalPath' x_axis x_label='Optimization steps' y_label='Number of tasks' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          <div className='chart'>
            <h4>Loss Function </h4>
            <StepsPlot results={data} name='loss' x_axis x_label='Optimization steps' y_label='Loss function' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          {/* <div className='chart'>
            <h4>Standard Deviation Labour Resources</h4>
            <StepsPlot results={optimizer?.results} name='rsrc_std' x_axis x_label='Optimization steps' y_label='Standard Deviation' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div>

          <div className='chart'>
            <h4>Number of Changed Tasks</h4>
            <StepsPlot results={optimizer?.results} name='num_changed' x_axis x_label='Optimization steps' y_label='Number of changed task' onClick={(index) => {
              handleSelectResultIndex(index)
            }} selectedResultIndex={selectedResultIndex} />
          </div> */}

        </OptimizationCharts>

        <OptimizationCharts className='multi-charts'>
          <div className='row'><h2>Resources Plots</h2></div>
          {Object.values(currentResources || {}).map((r, i) => {
            return (<div key={r.id} className='chart'>
              <h4>{`(${r.type}) ${r.name}`}</h4>
              <ResourcePlot dist={r.distribution.xy} title={`(${r.type}) ${r.name}`} color={plot_color_palette.ResourcePlot.allResources[i % plot_color_palette.ResourcePlot.allResources.length]} />
            </div>)
          }
          )}
        </OptimizationCharts>

      </>

      <div className='row'>
        <SecondaryButton variant='contained' onClick={goPrev}>
          Back
        </SecondaryButton>
        <PrimaryButton onClick={() => setOpenModal(true)} variant='contained'>
          Export Report
        </PrimaryButton>
      </div>
    </OptimizationContainer>

    <Modal isLoading={openModal} onClose={() => setOpenModal(false)} title="Export Optimized File" content={<div className='row'>
      <PrimaryButton onClick={() => {
        optimizer.reader.merge_data(optimizer.results[selectedResultIndex].inp)
        optimizer.reader.export()
        //reader.exportToOriginal(project);
      }} variant='contained'>Original Format</PrimaryButton> &nbsp;
      <PrimaryButton onClick={() => {
        optimizer.exportToExcel()
      }} variant='contained'>Excel</PrimaryButton>
    </div>} />
  </>
  )
}