import * as xlsx from "xlsx";

export function lastOf(array) {
  return array[array.length - 1];
}

export async function fetchFile(url = "") {
  let filename = url.split("/").pop();
  let res = await fetch(url);
  let blob = await res.blob();
  return new File([blob], filename);
}

export function toObject(keys = [], values) {
  let object = {};
  keys.forEach((key, i) => (object[key] = values[i]));
  return object;
}

export function len(x) {
  return Object.keys(x).length;
}

export function asArray(x) {
  return x ? (Array.isArray(x) ? x : [x]) : [];
}

export const parser = {
  parseDate(x) {
    return Date.parse(x + "Z");
  },
  strDate(x) {
    if (!x) return;
    let str = new Date(x).toISOString();
    return str.substring(0, 10) + " " + str.substring(11, 16);
  },
  parseDuration(x = "PT200H0M0S") {
    return parseInt(x.split("PT")[1].split("H")[0]);
  },
  strDuration(x) {
    return `PT${x}H0M0S`;
  },
};

export function writeExcel(tables, filename, maxWidth = 50) {
  let book = xlsx.utils.book_new();
  Object.entries(tables).forEach(([name, rows]) => {
    rows = Object.values(rows);
    let sheet = xlsx.utils.json_to_sheet(rows);
    sheet["!cols"] = Object.keys(rows[0]).map((columnName) => {
      let maxLen = Math.max(
        columnName.length,
        ...rows.map((row) => (row[columnName] ? String(row[columnName]).length : 0))
      );
      return { wch: Math.min(maxWidth, maxLen + 1) };
    });
    xlsx.utils.book_append_sheet(book, sheet, name);
  });
  xlsx.writeFile(book, filename);
}

export function readExcel(file = new File()) {
  return new Promise((resolve) => {
    let reader = new FileReader();
    reader.onload = (e) => {
      let book = xlsx.read(e.target.result, { type: "binary" });
      let tables = {};
      book.SheetNames.forEach((name) => {
        tables[name] = xlsx.utils.sheet_to_json(book.Sheets[name]);
      });
      resolve(tables);
    };
    reader.readAsBinaryString(file);
  });
}

export function exportFile(filename, text) {
  let e = document.createElement("a");
  let prefix = "data:text/plain;charset=utf-8,";
  e.setAttribute("href", prefix + encodeURIComponent(text));
  e.setAttribute("download", filename);
  e.style.display = "none";
  document.body.appendChild(e);
  e.click();
  document.body.removeChild(e);
}

export function clip(value, min, max) {
  if (min || min === 0) value = Math.max(min, value);
  if (max || max === 0) value = Math.min(max, value);
  return value;
}

export function addToFilename(filename = "", add = "") {
  let parts = filename.split(".");
  let extension = parts.pop();
  return parts.join(".") + add + "." + extension;
}
