export function toValue(x) {
  if (x instanceof Value) return x;
  return new Value(x);
}

export class Value {
  gradient = 0;
  _backward = () => {};

  constructor(data, _children = []) {
    this.data = data;
    this._previous = new Set(_children);
  }

  add(other) {
    other = toValue(other);
    let out = new Value(this.data + other.data, [this, other]);

    out._backward = () => {
      this.gradient += out.gradient;
      other.gradient += out.gradient;
    };

    return out;
  }

  mul(other) {
    other = toValue(other);
    let out = new Value(this.data * other.data, [this, other]);

    out._backward = () => {
      this.gradient += other.data * out.gradient;
      other.gradient += this.data * out.gradient;
    };

    return out;
  }

  pow(other) {
    let out = new Value(Math.pow(this.data, other), [this]);

    out._backward = () => {
      this.gradient += other * Math.pow(this.data, other - 1) * out.gradient;
    };

    return out;
  }

  round() {
    let out = new Value(Math.round(this.data), [this]);
    out._backward = () => {
      this.gradient += out.gradient;
    };
    return out;
  }

  backward() {
    let topological_order = [];
    let visited = new Set();

    let build_topology = (value = new Value(0)) => {
      if (!visited.has(value)) {
        visited.add(value);
        value._previous.forEach(build_topology);
        topological_order.push(value);
      }
    };
    build_topology(this);

    this.gradient = 1;
    topological_order.reverse().forEach((value) => {
      value._backward();
    });
  }

  sub(other) {
    other = toValue(other);
    return this.add(other.mul(-1));
  }

  div(other) {
    other = toValue(other);
    return this.mul(other.pow(-1));
  }
}

export class AdamOptimizer {
  constructor(parameters, lr = 1e-3, beta1 = 0.9, beta2 = 0.999, epsilon = 1e-8) {
    Object.assign(this, { parameters, lr, beta1, beta2, epsilon });
    this.m = {};
    this.v = {};
    Object.entries(parameters).forEach(([id, value]) => {
      this.m[id] = 0;
      this.v[id] = 0;
    });
    this.t = 0;
  }

  step(constrain = (id, value) => value) {
    let { parameters, lr, beta1, beta2, epsilon, m, v } = this;
    this.t += 1;
    Object.entries(parameters).forEach(([id, value]) => {
      m[id] = beta1 * m[id] + (1 - beta1) * value.gradient;
      v[id] = beta2 * v[id] + (1 - beta2) * Math.pow(value.gradient, 2);
      let mHat = m[id] / (1 - Math.pow(beta1, this.t));
      let vHat = v[id] / (1 - Math.pow(beta2, this.t));
      let newValue = value.data - (lr * mHat) / (Math.sqrt(vHat) + epsilon);
      parameters[id] = new Value(constrain(id, newValue));
    });
  }
}

export function findMinMaxSpan(values = [new Value(0)]) {
  values = Object.values(values);
  if (!values.length) return;
  let min = new Value(Infinity),
    max = new Value(-Infinity);
  values.forEach((value) => {
    if (value.data < min.data) min = value;
    if (value.data > max.data) max = value;
  });
  let span = max.sub(min);
  return { min, max, span };
}

export class Distribution {
  xy = {};
  min = new Value(0);
  max = new Value(0);
  span = new Value(0);

  add(x, y) {
    if (!this.xy[x]) this.xy[x] = new Value(0);
    this.xy[x] = this.xy[x].add(y);
  }

  evaluate() {
    Object.assign(this, findMinMaxSpan(this.xy));
  }
}

export function addRangeLoss(sampleMin, sampleMax, min, max, loss = new Value(0)) {
  if (sampleMin?.data < min) {
    loss = loss.add(sampleMin.sub(min).div(-min));
  }
  if (sampleMax?.data > max) {
    loss = loss.add(sampleMax.sub(max).div(max));
  }
  return loss;
}

export function extractData(x) {
  if (x instanceof Value) return x.data;
  else if (Array.isArray(x)) return x.map(extractData);
  else if (typeof x === "object" && x) {
    let newObject = {};
    Object.entries(x).forEach(([k, v]) => {
      newObject[k] = extractData(v);
    });
    return newObject;
  }
  return x;
}
