import { XMLBuilder, XMLParser } from 'fast-xml-parser'
import { Calendar, Project, Resource, Task, TaskPredecessor, TaskResource } from './models'
import { P6XerReader } from './P6XerReader'
import * as samples from './samples'
import { asArray, parser } from './utils'

const MS_PER_HOUR = 60 * 60 * 1000

export class MspXmlReader extends P6XerReader {
  rawTextToRawData(text = '') {
    let parser = new XMLParser({
      ignoreAttributes: false,
      ignoreDeclaration: false,
      ignorePiTags: false,
    })
    let rawData = parser.parse(text)
    return rawData
  }

  rawDataToProject(rawData) {
    let project = new Project()

    let p = rawData.Project

    p.Tasks.Task.forEach((x = samples.mspTask) => {
      let y = new Task()
      y.id = x.UID
      y.name = x.Name
      y.code = x.WBS
      y.type = x.Summary ? 'Summary' : x.Milestone ? 'Milestone' : 'Task'

      y.calendarId = x.CalendarUID
      y.predecessors = {}
      y.resources = {}

      y.plannedStart = parser.parseDate(x.Start)
      y.plannedFinish = parser.parseDate(x.Finish)
      y.plannedDuration = parser.parseDuration(x.Duration)

      y.actualStart = parser.parseDate(x.ActualStart)
      y.actualFinish = parser.parseDate(x.ActualFinish)

      project.tasks[y.id] = y

      asArray(x.PredecessorLink).forEach((a = samples.mspTaskPredecessor) => {
        let b = new TaskPredecessor()
        b.id = null
        b.taskId = y.id
        b.predecessorId = a.PredecessorUID

        b.type = ['FF', 'FS', 'SF', 'SS'][a.Type]
        b.lagHour = a.LinkLag / 600

        y.predecessors[b.predecessorId] = b
      })
    })

    asArray(p.Resources.Resource).forEach((x = samples.mspResource) => {
      let y = new Resource()
      y.id = x.UID
      y.name = x.Name
      y.code = x.Initials
      y.type = ['Material', 'Labor', 'Cost'][x.Type]

      project.resources[y.id] = y
    })

    let defaultCalendar = new Calendar()
    defaultCalendar.id = '-1'
    defaultCalendar.name = 'standard 5 days 8 hours'
    defaultCalendar.weekHourCount = 40
    project.calendars[defaultCalendar.id] = defaultCalendar

    const totalCalendars = p?.Calendars?.Calendar

    totalCalendars && totalCalendars.forEach((x = samples.mspCalendar) => {
      let y = new Calendar()
      y.id = x.UID
      y.name = x.Name
      y.weekHourCount = 0

      if (x?.WeekDays?.WeekDay !== undefined) {
        Object.values(x?.WeekDays?.WeekDay).forEach((day) => {
          let dayName = [null, 'Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'][day.DayType]
          if (dayName) {
            asArray(day.WorkingTimes?.WorkingTime).forEach((time) => {
              let date = '1971-06-28T'
              let ms = Date.parse(date + time.ToTime) - Date.parse(date + time.FromTime)
              let hour = ms / MS_PER_HOUR
              if (hour < 0) hour += 24
              y.weekHourCount += hour
            })
          }
        })
      }

      project.calendars[y.id] = y
    })

    const totalAssignments = p?.Assignments?.Assignment

    totalAssignments && totalAssignments.forEach((x = samples.mspTaskResource) => {
      let y = new TaskResource()
      y.id = x.UID
      y.taskId = x.TaskUID
      y.resourceId = x.ResourceUID

      y.plannedUnits = parser.parseDuration(x.Work)
      let task = project.tasks[y.taskId]
      y.plannedUnitsPerHour = task.plannedDuration > 0 ? y.plannedUnits / task.plannedDuration : 0

      task.resources[y.resourceId] = y
    })

    return project
  }

  projectToRawData(project) {
    let rawData = JSON.parse(JSON.stringify(this.rawData))
    let p = rawData.Project

    p.Tasks.Task.forEach((x = samples.mspTask) => {
      let y = project.tasks[x.UID]
      if (y) {
        x.Start = parser.strDate(y.newStart?.data)
        x.Finish = parser.strDate(y.newFinish?.data)
        x.Duration = parser.strDuration(y.newDuration?.data)

        if (x.ActualStart) x.ActualStart = parser.strDate(y.newStart?.data)
        if (x.ActualFinish) x.ActualFinish = parser.strDate(y.newFinish?.data)
      }
    })

    const totalAssignments = p.Assignments?.Assignment

    totalAssignments.forEach((x = samples.mspTaskResource) => {
      let y = project.tasks[x.TaskUID]?.resources[x.ResourceUID]
      if (y) {
        x.Units = x.PeekUnits = y.newUnitsPerHour?.data
      }
    })

    return rawData
  }

  rawDataToRawText(rawData) {
    let builder = new XMLBuilder({
      ignoreAttributes: false,
    })
    return builder.build(rawData)
  }
}
