import React, { useState, useEffect } from 'react'

import { Optimizer } from 'src/engine/Optimizer'
import { MspXmlReader } from 'src/engine/MspXmlReader'
import { P6XerReader } from 'src/engine/P6XerReader'

import { Slider, FormControl, FormControlLabel, Tooltip, Radio } from '@mui/material'
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined'
import UploadFileIcon from '@mui/icons-material/UploadFile'

import Modal from 'src/feat/Modal'

import { PrimaryButton } from 'src/styled'
import { Content, OptimizationContainer, InputField } from './styled'

const ratioMarks = [
  {
    value: 0,
    label: '0%',
  },
  {
    value: 100,
    label: '100%',
  },
  {
    value: 200,
    label: '200%',
  },
]

const stepMarks = [
  {
    value: 10,
    label: '10',
  },
  {
    value: 100,
    label: '100',
  },
  {
    value: 200,
    label: '200',
  },
  {
    value: 300,
    label: '300',
  },
  {
    value: 400,
    label: '400',
  },
  {
    value: 500,
    label: '500',
  },
]

export default function InputData({
  uploadedFile,
  setUploadedFile,
  fileName,
  setFileName,
  constraintsFileName,
  setConstraintsFileName,
  updateProject,
  optimizer,
  setOptimizer,
  goNext,
}) {
  const [isLoading, setIsLoading] = useState(false)
  const [isFileUploaded, setIsFileUploaded] = useState(false)
  const [project, setProject] = useState(null)

  const [considerDates, setConsiderDates] = useState(optimizer?.project?.settings ? optimizer.project.settings.considerActualDates : true)
  const [ratio, setRatio] = useState(optimizer?.project?.settings ? [optimizer.project.settings.minDurationRatio, optimizer.project.settings.maxDurationRatio] : [50, 150])
  const [steps, setSteps] = useState(optimizer?.project?.settings ? optimizer.project.settings.optimizationSteps : 20)
  const [learningRate, setLearningRate] = useState(optimizer?.project?.settings ? optimizer.project.settings.learningRate : 0.025)

  useEffect(() => {
    if (optimizer?.project?.settings) {
      optimizer.project.settings.minDurationRatio = ratio[0] / 100
      optimizer.project.settings.maxDurationRatio = ratio[1] / 100
    }
  }, [ratio])

  useEffect(() => {
    if (optimizer?.project?.settings) {
      optimizer.project.settings.optimizationSteps = steps
    }
  }, [steps])

  useEffect(() => {
    if (optimizer?.project?.settings) {
      optimizer.project.settings.considerActualDates = considerDates
    }
    updateProject(project)
  }, [considerDates])

  useEffect(() => {
    if (optimizer?.project?.settings) {
      optimizer.project.settings.learningRate = learningRate
    }
  }, [learningRate])

  const handleConsiderDatesChange = (value) => {
    if (value === 'true') {
      setConsiderDates(true)
    } else setConsiderDates(false)
  }

  const readProject = async (file) => {
    setUploadedFile(file)
    setFileName(file.name)

    let reader = file?.name.endsWith('xer') ? new P6XerReader() : new MspXmlReader()
    let project = await reader.read(file)
    setProject(project)
    setOptimizer(new Optimizer(project))
    setIsFileUploaded(true)
  }

  const readConstraints = async (file) => {
    setConstraintsFileName(file.name)
    await optimizer.applyConstraints(file)
  }

  useEffect(() => {
    if (uploadedFile) {
      readProject(uploadedFile)
    }
    updateProject(project)
  }, [uploadedFile])

  const useDemoProject = () => {
    fetch('/demo_project.xml')
      .then((r) => r.blob())
      .then((r) => {
        const file = new File([r], 'demo_project.xml')
        setUploadedFile(file)
        setIsFileUploaded(true)
      })
  }

  return (
    <Content>
      <h2>Upload schedule and enter optimization settings</h2>
      <OptimizationContainer>
        <div className="optimization-input">
          <div className="section">
            <div className="intro">
              <h2>Schedule</h2>
              <h3>Upload your schedule (.xer or .xml)</h3>
            </div>
            <div className="input-wrapper">
              <div className="inputs" style={{ alignItems: 'center' }}>
                <div className="upload-file">
                  <input
                    hidden
                    type="file"
                    id="project-file"
                    name="project"
                    onChange={(e) => {
                      setUploadedFile(e.target.files[0])
                    }}
                    accept=".xer,.xml"
                  />
                  <label className="upload-button" htmlFor="project-file">
                    <UploadFileIcon /> Upload file
                  </label>
                  <span>{fileName}</span>
                </div>
                <span>
                  or{' '}
                  <span
                    style={{ textDecoration: 'underline', cursor: 'pointer' }}
                    onClick={useDemoProject}
                  >
                    use a demo project
                  </span>
                </span>
              </div>
            </div>
          </div>
          <div className={`section ${isFileUploaded ? '' : 'disabled'}`}>
            <div className="intro">
              <h2>Constraints</h2>
              <h3>
                Upload your constraints (
                <span
                  style={{ textDecoration: 'underline', cursor: 'pointer' }}
                  onClick={() => {
                    optimizer.downloadConstraints()
                  }}
                >
                  download template
                </span>
                )
              </h3>
            </div>
            <div className="input-wrapper">
              <div className="inputs">
                <div className="upload-file">
                  <input
                    hidden
                    type="file"
                    id="constraints-file"
                    name="constraints"
                    onChange={(e) => {
                      readConstraints(e.target.files[0])
                    }}
                    accept=".xls,.xlsx"
                  />
                  <label
                    className="upload-button  second-upload"
                    htmlFor="constraints-file"
                  >
                    <UploadFileIcon /> Upload file
                  </label>
                  <span>{constraintsFileName}</span>
                </div>
              </div>
            </div>
          </div>
          <div className={`section ${isFileUploaded ? '' : 'disabled'}`}>
            <div className="intro">
              <h2>Optimization Settings</h2>
              <h3>Customize your optimization results</h3>
            </div>
            <div className="input-wrapper">
              <div className="inputs">
                <FormControl>
                  <label>Ignore Project Scheduled Dates
                    <Tooltip
                      title={
                        <div style={{ fontSize: '13px', fontFamily: 'Inter' }}>
                          The engine will not take into account any defined dates therefore the optimized project will not follow original schedule
                        </div>
                      }
                    >
                      <InfoOutlinedIcon />
                    </Tooltip></label>
                  <div className="row" style={{ justifyContent: 'flex-start' }}>
                    <FormControlLabel value={false} checked={considerDates === false} control={<Radio />} label="Yes" onChange={(e) =>
                      handleConsiderDatesChange(e.target.value)} />
                    <FormControlLabel value={true} checked={considerDates === true} control={<Radio />} label="No" onChange={(e) => handleConsiderDatesChange(e.target.value)} />
                  </div>
                </FormControl>
                <FormControl>
                  <label>
                    Learning Rate
                    <Tooltip
                      title={
                        <div style={{ fontSize: '13px', fontFamily: 'Inter' }}>
                          Learning rate is the step size of the engine which affects speed, stability, and convergence. The higher the rate, the faster results are generated.
                        </div>
                      }
                    >
                      <InfoOutlinedIcon />
                    </Tooltip>
                  </label>
                  <InputField
                    id="learningRate"
                    type="number"
                    value={learningRate}
                    onChange={(e) => {
                      setLearningRate(e.target.value)
                    }}
                    placeholder="eg: 0.025"
                    variant="outlined"
                    disabled={!isFileUploaded}
                  />
                </FormControl>
              </div>
              <div className="inputs">
                <FormControl style={{ width: '100%' }}>
                  <label>
                    Optimization Ratio {' '}
                    {ratio ? (
                      <>
                        ({ratio[0].toString()}% - {ratio[1].toString()}%)
                      </>
                    ) : (
                      ''
                    )}
                    <Tooltip
                      title={
                        <div style={{ fontSize: '13px', fontFamily: 'Inter' }}>
                          Ratio = New Duration over Baseline Duration
                        </div>
                      }
                    >
                      <InfoOutlinedIcon />
                    </Tooltip>
                  </label>

                  <Slider
                    id="optimization-ratio"
                    step={5}
                    min={1}
                    max={200}
                    valueLabelDisplay="auto"
                    getAriaLabel={() => 'Optimization Ratio'}
                    value={ratio}
                    marks={ratioMarks}
                    onChange={(e) => setRatio(e.target.value)}
                    disabled={!isFileUploaded}
                  />
                </FormControl>
              </div>
              <div className="inputs">
                <FormControl style={{ width: '100%' }}>
                  <label>Optimization Steps ({steps.toString()})
                    <Tooltip
                      title={
                        <div style={{ fontSize: '13px', fontFamily: 'Inter' }}>
                          Recommended steps: 20 - 50, the higher the number the longer it will take to optimize results
                        </div>
                      }
                    >
                      <InfoOutlinedIcon />
                    </Tooltip>
                  </label>

                  <Slider
                    id="optimization-steps"
                    aria-label="optimization-steps"
                    value={steps}
                    onChange={(e) => {
                      setSteps(e.target.value)
                    }}
                    step={1}
                    min={10}
                    max={500}
                    marks={stepMarks}
                    valueLabelDisplay="auto"
                    disabled={!isFileUploaded}
                  />
                </FormControl>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div />
          <PrimaryButton
            variant="contained"
            onClick={goNext}
            disabled={!isFileUploaded}
          >
            Next
          </PrimaryButton>
        </div>

        <Modal
          isLoading={isLoading}
          title={`Reading File Data (size: ${uploadedFile && uploadedFile.size / 1000000} Mb)`}
          content="This may take up to a few minutes."
          spinner
        />

      </OptimizationContainer >
    </Content >
  )
}
