import { Value, Distribution, AdamOptimizer, addRangeLoss, toValue, extractData } from "./autoGrad";
import { Project, Task } from "./models";
import { clip, len, parser, readExcel, writeExcel } from "./utils";

const MS_PER_HOUR = 60 * 60 * 1000;
const MS_PER_DAY = 24 * MS_PER_HOUR;
const MS_PER_WEEK = 7 * MS_PER_DAY;

export class Optimizer {
  project = new Project();
  results = {};

  constructor(project = new Project()) {
    this.project = project;
    this._ignoreTasks();
    this._reorderTasks();

    this.simulate();
    this.planned = this.project.output;
    this.plannedproject = JSON.parse(JSON.stringify(this.project))

    this.constraintTables = this._makeConstraints();
    console.log(project.projectFileName);
  }

  _ignoreTasks() {
    let { tasks } = this.project;
    Object.values(tasks).forEach((task) => {
      task.ignore = task.type !== "Task";
    });
    Object.values(tasks).forEach((task) => {
      Object.values(task.predecessors).forEach((x) => {
        let predecessor = tasks[x.predecessorId];
        if (predecessor.ignore) {
          delete task.predecessors[x.predecessorId];
        }
      });
    });
  }

  _reorderTasks() {
    let { tasks, resources, calendars } = this.project;
    let found = {};
    this.orderedTasks = [];

    const loopOnce = () => {
      Object.values(tasks).forEach((task) => {
        if (found[task.id]) return;
        if (task.ignore) found[task.id] = true;
        if (Object.values(task.predecessors).every((x) => found[x.predecessorId])) {
          found[task.id] = true;
          if (!task.ignore) this.orderedTasks.push(task);
        }
      });
    };

    while (1) {
      let lastLen = len(found);
      loopOnce();
      if (len(found) === lastLen) break;
    }

    if (len(found) !== len(tasks)) {
      throw new Error("predecessor error");
    } else {
      console.log(
        `topological reordering successful. ${len(this.orderedTasks)}/${len(tasks)} tasks considered. ${len(
          resources
        )} resources. ${len(calendars)} calendars.`
      );
    }
  }

  simulate(durationRatios = {}) {
    let { settings, calendars } = this.project;

    let projectStart = new Value(Infinity);
    let projectFinish = new Value(0);
    let criticalId;

    this.orderedTasks.forEach((task) => {
      task.criticalPredecessorId = null;
      task.critical = null;

      let msPerHour = MS_PER_WEEK / calendars[task.calendarId].weekHourCount;

      if (settings.considerActualDates && task.actualStart) {
        task.optimizable = false;
        task.newStart = new Value(task.actualStart);
        task.newFinish = new Value(task.actualFinish || task.plannedFinish);
        task.newDuration = new Value(task.plannedDuration);
      } else {
        task.optimizable = true;
        task.durationRatio = toValue(durationRatios[task.id] || 1);
        task.newDuration = task.durationRatio.mul(task.plannedDuration).round();
        let durationMs = task.newDuration.mul(msPerHour);
        if (len(task.predecessors)) {
          task.newStart = this.__findStartFromPredecessors(task, durationMs);
        } else {
          task.newStart = new Value(task.plannedStart);
        }
        task.newFinish = task.newStart.add(durationMs);
      }

      if (task.newStart.data < projectStart.data) {
        projectStart = task.newStart;
      }
      if (task.newFinish.data > projectFinish.data) {
        projectFinish = task.newFinish;
        criticalId = task.id;
      }
    });

    let projectDurationDays = projectFinish.sub(projectStart).div(MS_PER_DAY);
    let laborDistribution = this.__findResourceDistribution();
    let criticalPath = this.__findCriticalPath(criticalId);
    let constraintLoss = this.__findConstraintLoss();

    // console.log(
    //   `${Math.round(projectDurationDays.data)} days. labor: max ${Math.round(
    //     laborDistribution.max.data
    //   )}, span ${Math.round(laborDistribution.span.data)}. critical: ${len(criticalPath)}. constraint loss: ${constraintLoss.data
    //   }.`
    // );

    this.project.output = {
      projectStart,
      projectFinish,
      projectDurationDays,
      laborDistribution,
      criticalPath,
      constraintLoss,
    };
  }

  __findStartFromPredecessors(task = new Task(), durationMs) {
    let newStart = new Value(0);
    Object.values(task.predecessors).forEach((x) => {
      let predecessor = this.project.tasks[x.predecessorId];
      let lagMs = x.lagHour * MS_PER_HOUR;
      let start;
      if (x.type === "FS") {
        start = predecessor.newFinish.add(lagMs);
      } else if (x.type === "SS") {
        start = predecessor.newStart.add(lagMs);
      } else if (x.type === "FF") {
        start = predecessor.newFinish.add(lagMs).sub(durationMs);
      } else if (x.type === "SF") {
        start = predecessor.newStart.add(lagMs).sub(durationMs);
      }
      if (start.data > newStart.data) {
        newStart = start;
        task.criticalPredecessorId = predecessor.id;
      }
    });
    return newStart;
  }

  __findResourceDistribution() {
    let { resources, calendars } = this.project;
    Object.values(resources).forEach((resource) => {
      resource.distribution = new Distribution();
    });
    let laborDistribution = new Distribution();

    this.orderedTasks.forEach((task) => {
      if (!task.optimizable) return;
      if (task.newDuration.data === 0) return;

      let startDay = Math.floor(task.newStart.data / MS_PER_DAY);
      let finishDay = Math.floor(task.newFinish.data / MS_PER_DAY);
      let calendar = calendars[task.calendarId];

      Object.values(task.resources).forEach((x) => {
        if (x.plannedUnits === 0) return;

        let resource = resources[x.resourceId];
        if (!resource) return;

        x.newUnitsPerHour = new Value(x.plannedUnits).div(task.newDuration);
        if (resource.type === "Material") {
          x.newUnitsPerDay = x.newUnitsPerHour.mul(calendar.weekHourCount / 7);
        } else {
          x.newUnitsPerDay = x.newUnitsPerHour;
        }

        for (let day = startDay; day <= finishDay; day++) {
          let ts = day * MS_PER_DAY;
          resource.distribution.add(ts, x.newUnitsPerDay);
          if (resource.type === "Labor") {
            laborDistribution.add(ts, x.newUnitsPerDay);
          }
        }
      });
    });

    Object.values(resources).forEach((resource) => {
      resource.distribution.evaluate();
    });
    laborDistribution.evaluate();
    return laborDistribution;
  }

  __findCriticalPath(criticalId) {
    let criticalPath = [];
    while (criticalId) {
      criticalPath.push(criticalId);
      let task = this.project.tasks[criticalId];
      task.critical = true;
      criticalId = task.criticalPredecessorId;
    }
    return criticalPath;
  }

  __findConstraintLoss() {
    let loss = new Value(0);

    this.orderedTasks.forEach((task) => {
      Object.values(task.resources).forEach((x) => {
        loss = addRangeLoss(x.newUnitsPerDay, x.newUnitsPerDay, x.minUnitsPerDay, x.maxUnitsPerDay, loss);
      });
    });

    Object.values(this.project.resources).forEach((x) => {
      let d = x.distribution;
      loss = addRangeLoss(d.min, d.max, x.minUnitsPerDay, x.maxUnitsPerDay, loss);
    });

    return loss;
  }

  _makeConstraints() {
    let { planned, project } = this;
    let { tasks, resources, calendars, output } = project;
    let newTasks = [];
    let newTaskResources = [];
    let newResources = [];

    const convertOutput = (out) => ({
      projectStart: parser.strDate(out.projectStart.data),
      projectFinish: parser.strDate(out.projectFinish.data),
      projectDurationDays: Math.round(out.projectDurationDays.data),
      laborDistributionDays: len(out.laborDistribution.xy),
      laborDistributionMin: out.laborDistribution.min.data,
      laborDistributionMax: out.laborDistribution.max.data,
      laborDistributionSpan: out.laborDistribution.span.data,
      criticalPath: out.criticalPath.map((id) => tasks[id].code).join(", "),
      constraintLoss: out.constraintLoss.data,
    });

    let newPlanned = [convertOutput(planned)];
    let newOutput = [convertOutput(output)];

    Object.values(tasks).forEach((task) => {
      let calendar = calendars[task.calendarId];
      let newTask = {
        taskInfo: "|||",
        id: task.id,
        type: task.type,
        ignore: task.ignore,
        code: task.code,
        name: task.name,

        calendarInfo: "|||",
        weekHourCount: calendar.weekHourCount,

        relationInfo: "|||",
        predecessors: Object.values(task.predecessors)
          .map((x) => tasks[x.predecessorId].code)
          .join(", "),
        resources: Object.values(task.resources)
          .map((x) => resources[x.resourceId]?.code)
          .join(", "),

        plannedInfo: "|||",
        plannedDuration: task.plannedDuration,
        plannedStart: parser.strDate(task.plannedStart),
        plannedFinish: parser.strDate(task.plannedFinish),

        actualInfo: "|||",
        actualStart: parser.strDate(task.actualStart),
        actualFinish: parser.strDate(task.actualFinish),

        frontlineInfo: "|||",
        durationRatio: task.durationRatio?.data,
        newDuration: task.newDuration?.data,
        newStart: parser.strDate(task.newStart?.data),
        newFinish: parser.strDate(task.newFinish?.data),

        criticalPathInfo: "|||",
        criticalPredecessor: tasks[task.criticalPredecessorId]?.code,
        critical: task.critical,

        constraintInfo: "|||",
        minDuration: null,
        maxDuration: null,
      };
      newTasks.push(newTask);

      Object.values(task.resources).forEach((x) => {
        let resource = resources[x.resourceId];
        if (!resource) return;
        let newUnitsPerHour = x.newUnitsPerHour?.data || 0;
        let newUnitsPerDay = x.newUnitsPerDay?.data || 0;

        let newTaskResource = {
          id: x.id,

          taskInfo: "|||",
          taskId: x.taskId,
          taskType: task.type,
          taskCode: task.code,

          resourceInfo: "|||",
          resourceId: x.resourceId,
          resourceType: resource.type,
          resourceCode: resource.code,

          calendarInfo: "|||",
          weekHourCount: calendar.weekHourCount,

          plannedInfo: "|||",
          plannedUnits: x.plannedUnits,
          plannedUnitsPerHour: x.plannedUnitsPerHour,

          frontlineInfo: "|||",
          newDuration: task.newDuration?.data,
          newUnitsPerHour,
          newUnitsPerDay,

          constraintInfo: "|||",
          minUnitsPerDay: null,
          maxUnitsPerDay: null,
        };
        newTaskResources.push(newTaskResource);
      });
    });

    Object.values(resources).forEach((resource) => {
      let d = resource.distribution;
      let newResource = {
        resourceInfo: "|||",
        id: resource.id,
        type: resource.type,
        code: resource.code,
        name: resource.name,

        distributionInfo: "|||",
        distributionDays: len(d.xy),
        distributionMin: d.min.data,
        distributionMax: d.max.data,
        distributionSpan: d.span.data,

        constraintInfo: "|||",
        minUnitsPerDay: null,
        maxUnitsPerDay: null,
      };
      newResources.push(newResource);
    });

    return {
      tasks: newTasks,
      resources: newResources,
      taskResources: newTaskResources,
      plannedProperties: newPlanned,
      newProperties: newOutput,
    };
  }

  downloadConstraints() {
    let name = this.project.projectFileName + "_FrontlineConstraints.xlsx";
    writeExcel(this.constraintTables, name);
  }

  exportToExcel() {
    let name = this.project.projectFileName + "_FrontlineExport.xlsx";
    writeExcel(this._makeConstraints(), name);
  }

  async applyConstraints(file = new File()) {
    let { tasks, resources } = this.project;
    let tables = await readExcel(file);

    tables.tasks.forEach((x) => {
      let y = tasks[x.id];
      if (y) {
        y.minDuration = x.minDuration;
        y.maxDuration = x.maxDuration;
      }
    });

    tables.resources.forEach((x) => {
      let y = resources[x.id];
      if (y) {
        y.minUnitsPerDay = x.minUnitsPerDay;
        y.maxUnitsPerDay = x.maxUnitsPerDay;
      }
    });

    tables.taskResources.forEach((x) => {
      let y = tasks[x.taskId]?.resources[x.resourceId];
      if (y) {
        y.minUnitsPerDay = x.minUnitsPerDay;
        y.maxUnitsPerDay = x.maxUnitsPerDay;
      }
    });
  }

  async optimize(name, getValuesToMinimize) {
    return new Promise(async (resolve) => {
      let { project, results } = this;
      let { settings, tasks } = project;

      if (!results[name]) results[name] = [];

      let durationRatios = {};
      this.orderedTasks.forEach((task) => {
        durationRatios[task.id] = new Value(1);
      });

      let scales = {};

      let optimizer = new AdamOptimizer(durationRatios, settings.learningRate);

      let t1 = Date.now();
      let lastLoss, count;

      for (let i = 0; i < settings.optimizationSteps; i++) {
        await new Promise((resolve) => setTimeout(resolve, 0));
        await this.simulate(durationRatios);

        let values = [project.output.constraintLoss, ...getValuesToMinimize(project)];
        // console.log(values.map((v) => v.data));

        let loss = new Value(0);
        values.forEach((value, i) => {
          if (value.data > 0) {
            let amplitude = i === 0 ? 10 : 1;
            if (scales[i] === undefined) scales[i] = amplitude / value.data;
            loss = loss.add(value.mul(scales[i]));
          }
        });

        if (loss.data !== lastLoss) {
          lastLoss = loss.data;
          count = 0;
        } else count++;
        if (count > 10) break;

        results[name].push(
          extractData({
            durationRatios,
            output: project.output,
            loss,
          })
        );
        results[name].sort((a, b) => b.loss - a.loss);

        loss.backward();
        optimizer.step((id, newRatio) => {
          let task = tasks[id];
          newRatio = clip(newRatio, task.minDuration / task.plannedDuration, task.maxDuration / task.plannedDuration);
          newRatio = clip(newRatio, settings.minDurationRatio, settings.maxDurationRatio);
          return newRatio;
        });
        // send callback after each result is loaded
        let currentResults = results[name];
        let currentResult = currentResults[currentResults.length - 1];
        resolve(currentResult);
      }

      let t2 = Date.now();
      console.log(`${name}`, "took", t2 - t1, "ms");
      // console.log(results);
    });
  }

  async optimizeDefaults() {
    await this.optimize("Balanced", (project) => [
      project.output.projectDurationDays,
      project.output.laborDistribution.max,
      project.output.laborDistribution.span,
    ]);
    await this.optimize("Fastest", (project) => [project.output.projectDurationDays]);
    await this.optimize("Minimum Resources", (project) => [project.output.laborDistribution.max]);
    await this.optimize("Levelled Resources", (project) => [project.output.laborDistribution.span]);
    await this.optimize("Constraint Compliance", () => []);
  }
}