import React, { useState, useEffect } from 'react'

import { FormControlLabel, FormControl, Radio, RadioGroup } from '@mui/material'

import { plot_color_palette } from '../../../utils/color'

import TaskPlot from '../../../components/Plot/TaskPlot'
import DistributionPlot from '../../../components/Plot/Distribution'

import { PrimaryButton } from 'src/styled'
import { OptimizationContainer, OptimizationCharts, LoadingTitle } from './styled'

export default function BaselineSummary({ optimizer, constraintsFileName, baseline, selectedPreset, setSelectedPreset, selectedResultIndex, setSelectedResultIndex, goNext }) {

  const [currentTasks, setCurrentTasks] = useState(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
  const baselineTasks = Object.values(optimizer?.plannedproject?.tasks).filter((i) => i.newFinish)

  const [baselineResults, setBaselineResults] = useState(optimizer?.plannedproject)
  const [steps, setSteps] = useState(optimizer?.project?.settings?.optimizationSteps)

  const [data, setData] = useState(optimizer?.results)
  const [listOfPresets, setListOfPresets] = useState(Object.keys(optimizer?.results))
  const [selectedPresetResult, setSelectedPresetResult] = useState(optimizer?.results[selectedPreset])

  useEffect(() => {
    if (optimizer?.results['Balanced'] === undefined) {
      setTimeout(() => {
        optimizer.optimizeDefaults()
        setSelectedResultIndex(selectedPresetResult?.length - 1)
        setBaselineResults(optimizer?.plannedproject)
        setSteps(optimizer?.project?.settings?.optimizationSteps)
      }, 100)
      setTimeout(() => {
        setSelectedPresetResult(optimizer?.results[selectedPreset])
        setSelectedResultIndex(optimizer?.results[selectedPreset].length - 1)
      }, 2500)
    }
    else return null
  }, [])

  useEffect(() => {
    const renderUi = setInterval(() => {
      setData(optimizer?.results)
      setListOfPresets(Object.keys(optimizer?.results));
      setCurrentTasks(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
    }, 1000);

    return () => clearInterval(renderUi);
  }, [])

  useEffect(() => {
    setCurrentTasks(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
    if (optimizer && selectedPresetResult?.length - 1 === selectedResultIndex) {
      optimizer?.simulate(selectedPresetResult?.at(selectedResultIndex).durationRatios)
    }
  }, [selectedPresetResult]) // preset changes here 

  const statData = (title, numbers) => {
    return <div className='stats'>
      <div className='title'>{title}
      </div>
      <div className='figure'>
        <div className='numbers'>{numbers}</div>
      </div>
    </div >
  }

  const renderSwitch = (label) => {
    switch (label) {
      case 'Balanced':
        return `Best combination of the fastest, minimum and levelled resources`
      case 'Fastest':
        return `Shortest project duration`
      case 'Minimum Resources':
        return `Least amount of required resources`
      case 'Levelled Resources':
        return `Best resource distribution`
      case 'Constraint Compliance':
        return `Satisfies all constraints`
      // return `Satisfies all constraints ${data[label].length}`
      default:
        return ''
    }
  }

  const buttonClick = () => {
    setSelectedResultIndex(optimizer?.results[selectedPreset].length - 1)
    goNext()
  }

  return <>
    <OptimizationContainer>
      <h2>Baseline Summary</h2>
      <div className='summary'>
        {statData('Project Duration (days)', Math.round(baseline?.duration))}
        {statData('Resources', Math.round(baseline?.resources?.length))}
        {statData('Tasks', Object.values(baseline?.tasks).length)}
        {statData('Tasks on Critical Path', Math.round(baseline?.critical_paths?.length))}
      </div >

      <OptimizationCharts className="multi-charts">
        <div className='full-chart'>
          <div className='section'>
            {data === 0 || data === undefined ? <h3><LoadingTitle><span>.</span><span>.</span><span>.</span></LoadingTitle></h3> : <h3>{selectedPreset} Tasks Overview</h3>}
            <div className='legend'>
              <div className='item'>
                <div style={{ width: '14px', height: '14px', background: plot_color_palette.ComparisonPlot[selectedPreset], marginRight: '5px', borderRadius: '50%' }}></div>
                <div>{selectedPreset}</div>
              </div>
              <div className='item'>
                <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
                <div>Baseline</div>
              </div>
              <div className='item'>
                <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                <div>Critical Path</div>
              </div>
              {/*
                    {len(optimizationPresets[loadingPreset ? loadingPreset : selectedPreset].optimizer.results.at(-1)?.inp?.tasks || {}) <= 400 ?
                      <div className='item'>
                        <div style={{ width: '14px', height: '14px', background: '#e61717', marginRight: '5px', borderRadius: '50%' }}></div>
                        <div>Critical Path</div>
                      </div>
                      : <></>
                    } */}
            </div>
            <TaskPlot
              tasks={currentTasks || []}
              width='1150'
              height='650'
              color={plot_color_palette.ComparisonPlot[selectedPreset]}
              baselineTasks={baselineTasks || []}
              title='Tasks (Overview)' />
          </div>
        </div>
      </OptimizationCharts>
      <OptimizationCharts>
        <div className='chart'>
          <div className='section'>
            <h3>Comparison Chart</h3>
            <div className='legend'>
              <div className='item'>
                <div style={{ width: '14px', height: '14px', background: '#cccccc', marginRight: '5px', borderRadius: '50%' }}></div>
                <div>Baseline</div>
              </div>

              {listOfPresets.map((p) => {
                return <div key={p} className={`item ${constraintsFileName === "No file chosen" && p === "Constraint Compliance" ? 'hidden' : ''}`}>
                  <div style={{ width: '14px', height: '14px', background: plot_color_palette.ComparisonPlot[p], marginRight: '5px', borderRadius: '50%' }}></div>
                  <div>{p}</div>
                </div>
              })}
            </div>

            <DistributionPlot
              key={selectedPresetResult}
              constraintsFileName={constraintsFileName}
              results={data || []}
              baselineResults={baselineResults || []}
              steps={steps}
              // optimizationPresets={optimizationPresets}
              selectedPreset={selectedPreset}
              selectedResultIndex={selectedResultIndex}
              x_label='Duration (days)'
              y_label='Maximum Resource (units)'
              onClick={(preset, i) => {
                setSelectedPreset(preset)
                setSelectedResultIndex(i)
              }}
            />
          </div>
        </div>

        <div className='chart'>
          <div className='section'>
            <h3>Optimization Presets</h3>
            <p>These available presets are pre-configured settings that are designed to deliver the best performance or quality for each individual specific use case.</p>
            {/* <p>Please select from one of the following options to generate your optimized report: </p> */}
            <FormControl component='fieldset'>
              <RadioGroup
                value={selectedPreset}
                onChange={(e) => {
                  setSelectedPreset(e.target.value)
                  setSelectedResultIndex(optimizer?.results[e.target.value].length - 1)
                }}
              >
                {listOfPresets.map((o) => {
                  return <div key={o} className={`row presets ${constraintsFileName === "No file chosen" && o === "Constraint Compliance" ? 'hidden' : ''}`}>
                    <FormControlLabel disabled={data[o].length <= 10} value={o} control={<Radio />} label={o} />
                    <div style={{ fontSize: '12px', fontFamily: 'Inter' }}>
                      {renderSwitch(o)}
                    </div>
                  </div>
                })}
              </RadioGroup>
            </FormControl>
            <div className='row'>
              <div />
              <PrimaryButton variant='contained' onClick={buttonClick}>
                View Optimized Summary
              </PrimaryButton>
            </div>
          </div>
        </div>
      </OptimizationCharts>

    </OptimizationContainer>

  </>
}